import React from 'react';
import { connect } from 'react-redux';
import { setTextFilter } from '../actions/filterText';
import axios from 'axios';
export class ApiFilters extends React.Component {
  
state = {
  currency:null,
  error:""
}

  onTextChange = (e) => {
    this.props.setTextFilter(e.target.value);
  };

  onSubmit = (e) => {
    axios.get(`https://api.coinmarketcap.com/v1/ticker/${this.props.text.text}/`)
      .then((response) => {
        if (response.status === 200) {

          this.setState({
            currency:response.data,
            error:''
          });
          
        }

      }).catch((e) => {
        this.setState({
          currency:null,
          error:e
        })
      });
  }


  
  render() {

      let result = '';

      if(this.state.error !== null || this.state.error !== ''){

        result = this.state.error;

      }else{


        result = this.state.currency[0].name


      }

      return (
      <div>
        <input
          type="text"
          value={this.props.text.text + ""}
          onChange={this.onTextChange}
        />
        <input type="submit"
          onClick={this.onSubmit}
          />
          <p>{result}</p>
      </div>
    );


    
    
  }
};



const mapStateToProps = (state) => ({
 text: state.text
});

const mapDispatchToProps = (dispatch) => ({
  setTextFilter: (text) => dispatch(setTextFilter(text))
});

export default connect(mapStateToProps, mapDispatchToProps)(ApiFilters);


